<template>
  <div class="chat-home-root">
    <img
      @dblclick="showRecharge"
      class="list-banner"
      :src="require('@/assets/banner.png')"
    />
    <!-- <van-nav-bar title="Title" fixed :border="false" /> -->
    <van-pull-refresh
      v-show="chats && chats.length > 0"
      pulling-text="Pull down to refresh..."
      loosing-text="Release to refresh..."
      loading-text="Loading..."
      v-model="refreshing"
      @refresh="onRefresh"
    >
      <!-- <div class="more-to-personal" @click="moreToPersonal">More ></div> -->
      <van-list
        loading-text="Loading..."
        finished-text="Loading..."
        v-model="loading"
        :finished="finished"
        @load="onLoad"
        class="card-list"
      >
        <div
          class="chat-card-wrap"
          v-for="(_, i) in chats"
          :key="i"
          @click="goChatDetail(_)"
        >
          <ChatCard :chat="_" :userdiamond="profile.diamond" />
        </div>
      </van-list>
    </van-pull-refresh>
    <div class="loading-list" v-if="!chats || chats.length < 1">
      <van-loading size="24px" color="#1989fa" vertical>Loading...</van-loading>
    </div>
    <Connect
      :uid="curHost.uid"
      :poster="curHost.avatar"
      @close="closeConnect"
      :key="Date.now()"
      v-if="connectShow"
    />
    <!-- <div class="recharge-tips-modal" v-if="showRechargeTips">
      <div class="to-recharge-dialog">
        <div class="close" @click="showRechargeTips = false">
          <van-icon name="close" />
        </div>
        <h2>You coins are running out</h2>
        <p>To continue video call</p>
        <div class="button">
          <div @click="rechargeNow">Recharge now</div>
        </div>
      </div>
    </div> -->

    <!-- <RechargeDialog
      :open="showRechargeTips"
      @confirm="rechargeNow"
      @cancel="showRechargeTips = false"
    ></RechargeDialog> -->
    <!-- <van-overlay :show="showRechargeTips" class="recharge-overlay"> -->
    <RechargeCard
      v-if="showRechargeTips"
      class="recharge-dialog"
      @leftRecharge="showRechargeTips = false"
    >
    </RechargeCard>
    <!-- </van-overlay> -->
  </div>
</template>

<script>
import Vue from "vue";
import { PullRefresh, List, Loading } from "vant";
import ChatCard from "../components/ChatCard";
import * as Server from "@/server";
import Connect from "../components/Connect";
// import RechargeDialog from "../components/dialog/RechargeDialog.vue";
// import router from '../router';
import RechargeCard from "../components/dialog/RechargeCard";

Vue.use(PullRefresh).use(List).use(Loading);
// .use(NavBar);
export default {
  components: {
    ChatCard,
    Connect,
    // RechargeDialog,
    RechargeCard,
  },
  data() {
    return {
      page: 1,
      chats: [],
      refreshing: false,
      loading: false,
      finished: true,
      profile: {},
      lastDBBanner: 0,
      connectShow: false,
      curHost: {},
      ruid: Math.random().toString().substr(2),
    };
  },
  created: function () {
    console.log("Home Created");
  },
  methods: {
    closeConnect() {
      this.connectShow = false;
    },
    showRecharge() {
      if (Date.now() - this.lastDBBanner < 1000) {
        this.$router.push("/recharge?showInfo");
        console.log("Router push recharge");
      }
      this.lastDBBanner = Date.now();
    },
    moreToPersonal() {
      this.$router.push("/recharge");
    },
    getUrlparam(key, url) {
      if (!url) url = window.location.href;
      let index = url.indexOf("?");
      if (index < 0) return null;
      url = url.substr(index + 1);
      let arr = url.split("&");
      let result = null;
      arr.forEach((value) => {
        let cur = value.split("=");
        if (cur[0] === key) {
          result = cur[1];
          return;
        }
      });
      return result;
    },
    async countAccess() {
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      let uid = JSON.parse(localStorage.getItem("profile") || "{}").uid;
      if (!trackInfo.source) trackInfo.source = trackInfo.appID || 305;
      if (!trackInfo.refby) trackInfo.refby = trackInfo.appID || 305;
      let { status, msg } = await Server.countAccess(
        uid,
        trackInfo.source,
        trackInfo.refby
      );
      console.log("CountAccess: ", status, msg);
    },
    async loadData() {
      const { data, status } = await Server.HomeList(this.ruid, this.page);
      this.refreshing = false;
      this.loading = false;
      if (status === 0) {
        const { records, total } = data;
        if (this.page === 1) {
          this.chats = records || [];
        } else {
          this.chats = this.chats.concat(records || []);
        }
        if (this.chats.length >= total) {
          this.finished = true;
        } else {
          this.finished = false;
        }
      }
      // this.countAccess();
    },
    onLoad() {
      this.page = this.page + 1;
      this.loadData();
    },
    onRefresh() {
      this.page = 1;
      this.loadData();
    },
    goChatDetail(item) {
      console.log("chat detail => ", item);
      // this.$router.push(`chat-detail/${item.uid}`);
      // this.$router.push(`chat-detail/3311605876848841`);
    },
    async queryBalance() {
      let { status, data } = await Server.QueryBalance(this.profile.uid);
      console.log("QueryBalance => ", status, data);
      return data;
    },
    rechargeNow() {
      this.$router.push("/recharge?showInfo");
    },
    async handleFrom() {
      let from = this.getUrlparam("from");
      if (!from) return;
      let uid = this.getUrlparam("uid");
      if (!uid) return;
      let token = localStorage.getItem("token");
      if (!token) {
        // this.$toast("Please Login");
        // this.$router.push("/login");
        return;
      }
      let balance = await this.queryBalance();
      console.log("HandlerFrom: Balance => ", balance);
      if (!balance || balance < 100) {
        this.showRechargeTips = true;
        return;
      }
      this.curHost.uid = uid;
      this.curHost.avatar = require("@/assets/logo.png");
      this.connectShow = true;
    },
  },
  mounted() {
    this.loadData();
    this.handleFrom();
  },
  watch: {
    $route(to, from) {
      console.log("Router change: ", to, " => ", from);
      if (/\/login/gi.test(from.path)) {
        console.log("From Login, Refresh List.", new Date().toString());
        this.chats = [];
        this.page = 1;
        this.loadData();
      }
    },
  },
};
</script>

<style lang="less">
.chat-home-root {
  padding: 10px 10px 0 10px;
  background-clip: #fff;
  .list-banner {
    width: 100%;
    margin-bottom: 10px;
  }
  .more-to-personal {
    text-align: right;
    font-size: 14px;
    line-height: 20px;
    color: #262626;
  }
  .card-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  .loading-list {
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>
